body {
	margin: 0;
	padding: 0;
	font-family: 'Cabin Condensed', sans-serif!important;
	-moz-osx-font-smoothing: grayscale;
}
:root {
	--input-padding-x: 1.5rem;
	--input-padding-y: 0.75rem;
}
.login,.image {
	min-height: 100vh;
}
.bg-image-signup {
	background-image: url('https://www.jetsetter.com/uploads/sites/7/2018/04/D6F790tO.jpeg');
	background-size: cover;
	background-position: center;
}
.bg-image-signin {
	background-image: url('https://www.libertytravel.com/sites/default/files/styles/full_size/public/luxury-hero%20%281%29.jpg?itok=eHbThPZQ');
	background-size: cover;
	background-position: center;
}
.bg-image-newvacation{
	background-image: url('https://hbr.org/resources/images/article_assets/2016/07/jul16-06-94978775.jpg');
	background-size: cover;
	background-position: center;
}
.login-heading {
	font-weight: 300;
}
.btn-login {
	font-size: 0.9rem;
	letter-spacing: 0.05rem;
	padding: 0.75rem 1rem;
	border-radius: 2rem;
}
.form-label-group {
	position: relative;
	margin-bottom: 1rem;
}
.form-label-group>input,.form-label-group>label {
	padding: var(--input-padding-y) var(--input-padding-x);
	height: auto;
	border-radius: 2rem;
}
.form-label-group>label {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	margin-bottom: 0;
	/* Override default `<label>` margin */
	line-height: 1.5;
	color: #495057;
	cursor: text;
	/* Match the input under the label */
	border: 1px solid transparent;
	border-radius: .25rem;
	transition: all .1s ease-in-out;
}
.form-label-group input::-webkit-input-placeholder {
	color: transparent;
}
.form-label-group input:-ms-input-placeholder {
	color: transparent;
}
.form-label-group input::-ms-input-placeholder {
	color: transparent;
}
.form-label-group input::-moz-placeholder {
	color: transparent;
}
.form-label-group input::placeholder {
	color: transparent;
}
.form-label-group input:not(:placeholder-shown) {
	padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));
	padding-bottom: calc(var(--input-padding-y) / 3);
}
.form-label-group input:not(:placeholder-shown)~label {
	padding-top: calc(var(--input-padding-y) / 3);
	padding-bottom: calc(var(--input-padding-y) / 3);
	font-size: 12px;
	color: #777;
}
.inputerr {
	border: 2px solid red;
}
